.Reasons{
    padding:0 2 rem;
    display:flex;
    gap:2rem;
}

.left-r{
    display: grid;
    grid-template-columns: repeat(3, 1fr); 
    grid-template-rows: auto auto;
    gap: 1rem;
}

.left-r>img{
    object-fit: cover;
}

.left-r > :nth-child(1) {
    width: 100%;
    height: 28rem;
    grid-column: 1 / 2;
    grid-row: 1 / 3;
    margin-left: 1rem;
}

.left-r > :nth-child(2) {
    width: 100%;
    height: 16rem;
    grid-column: 2 / 4;
    grid-row: 1;
    margin-left: 1rem;
}

.left-r > :nth-child(3) {
    width: 100%;
    height: 10rem;
    grid-column: 2 / 3;
    grid-row: 2;
    margin-left: 1rem;
}

.left-r > :nth-child(4) {
    width: 100%;
    height: 10rem;
    grid-column: 3 / 4;
    grid-row: 2;
    margin-left: 1rem;
}


.right-r{
    flex:1 1;
    text-transform: uppercase;
    gap:1 rem;
    display:flex;
    flex-direction: column;
}

.right-r>span{
    font-weight:bold;
    color:var(--orange);
}

.right-r>div{
    color:white;
    font-size:3rem;
    font-weight: bold;
}

.details-r{
    display:flex;
    flex-direction: column;
    gap:1rem;
}

.details-r>div{
    display:flex;
    font-size: 1rem;
    gap: 1rem;
}

.details-r>div>img{
   width:2rem;
   height:2rem; 
}

.partner{
    display:flex;
    gap:1rem;
}

partners>img{
    width:2.5rem;
}