.hero{
    display: flex;
    justify-content: space-between;
    height: 100vh;
}

.left-h{
    padding: 2rem;
    padding-top: 1.5rem;
    flex: 3;
    display: flex;
    gap: 2rem;
    flex-direction: column;
}


.right-h{
    flex: 1;
    position: relative;
    background-color: var(--orange);
    height: 100%;
}

.the-best-ad{
    margin-top: 4rem;
    background-color:#363d42;
    border-radius: 4rem;
    width: fit-content;
    padding: 20px 13px;
    text-transform: uppercase;
    color: white;
    position:relative;
    display:flex;
    align-items:center;
    justify-content:flex-start;
}
.the-best-ad>span{
    z-index:2;
}

.the-best-ad>div{
    position: absolute;
    background-color:var(--orange);
    width: 5.4rem;
    height: 80%;
    left: 8px;
    border-radius: 3rem;
    z-index:1;
}

.hero-text{
    display:flex;
    flex-direction:column;
    gap:1.5rem;
    font-size:4.5rem;
    font-weight:bold;
    color:white;
    text-overflow:inherit;
}

.hero-text>div:nth-of-type(3){
    font-size: 1rem;
    font-weight: 200;
    text-decoration: none;
    letter-spacing: 1px;
    width: 80%;
}

.figures>div>span:nth-of-type(1){
    color:white;
    font-size:2rem;
}

.figures {
    display: flex;
    gap: 2rem;
    margin-top: 1rem;
}
.figures>div>span:nth-of-type(2){
    color: var(--gray);
}

.buttons{
    display: flex;
    gap:1rem;
    font-weight:normal;
}

.buttons>:nth-child(1){
    color:white;
    background-color: var(--orange);
    width:8rem;
}

.buttons>:nth-child(2){
    color:white;
    background-color: transparent;
    width:8rem;
    border: 2px solid var(--orange);
}

.right-h>.btn{
    position:absolute;
    right:3rem;
    top:2rem;
    color:black;
}

.heart-rate{
    display:flex;
    flex-direction: column;
    gap: 1rem;
    background-color: var(--darkGrey);
    width: fit-content;
    padding: 1rem;
    align-items: start;
    border-radius: 5px;
    position: absolute;
    right:4rem;
    top:7rem;
}

.heart_rate>img{
    width:2rem;
}

.heart-rate>:nth-child(2){
    color:var(--gray);
}

.heart-rate>:nth-child(3){
    color:white;
    font-size:1.5rem;
}

.hero-image{
    position:absolute;
    top:10rem;
    right:8rem;
    width:23rem;
}

.hero-image-back{
    position:absolute;
    top:4rem;
    right:20rem;
    z-index:-1;
    width:15rem;
}

.calories {
    display: flex;
    gap: 2rem;
    background-color: var(--caloryCard);
    padding: 1rem;
    border-radius: 5px;
    position: absolute;
    top: 32rem;          
    right: 28rem;         
    width:fit-content;
    padding: 1rem;
    border-radius: 5px;
}

.calories>img {
    width: 3rem;
}

.calories>div{
    display:flex;
    flex-direction:column;
    justify-content:space-between;
}

.calories>div>:nth-child(1){
    color:var(--gray);
}

.calories>div>:nth-child(2){
    color:white;
    font-size:1.5rem;
}

.blur-h{
    width:22rem;
    height:30rem;
    left:0;
}







